<template>
  <div>
    <!--搜索条件中下拉框-->
    <el-select v-model="itemSelect" style="width: 120px">
      <el-option v-for="item in searchCondition" :key="item.value" :label="item.label"
                 :value="item.value">
      </el-option>
    </el-select>
    <el-input placeholder="输入关键字进行自动筛选" style="width: 200px"
              v-model="searchKeys" clearable/>
    <el-button icon="el-icon-search" plain @click="searchInfo">查询</el-button>
    <el-button icon="el-icon-refresh" plain @click="reset">重置</el-button>
    <!--表格主体内容部分 设置max-height需要设置height 否则不起作用-->
    <el-table :data="tableData" stripe border max-height="500" height="450"
              v-loading="loading"
              element-loading-text="拼命加载中"
              element-loading-spinner="el-icon-loading"
              element-loading-background="rgba(0, 0, 0, 0.8">
      <el-table-column label="序号" type="index" width="50"/>
      <el-table-column prop="name" label="姓名" width="100"/>
      <el-table-column prop="juniorNumber" label="专科学号"/>
      <el-table-column prop="juniorGrade" label="专科年级" width="100"/>
      <el-table-column prop="mainAcademy" label="主考院校"/>
      <el-table-column prop="sex" label="性别" width="50"/>
      <el-table-column prop="majorName" label="报考专业"/>
      <el-table-column prop="department" label="所在系部"/>
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label="学生姓名:">
              <span>{{ props.row.name }}</span>
            </el-form-item>
            <el-form-item label="身份证号:">
              <span>{{ props.row.idNumber }}</span>
            </el-form-item>
            <el-form-item label="学生性别:">
              <span v-if="props.row.sex=='女'" style="color: red">
                {{ props.row.sex }}
              </span>
              <span v-else style="color: blue">
                 {{ props.row.sex }}
              </span>
            </el-form-item>
            <el-form-item label="出生日期:">
              <span>{{ props.row.birthDate }}</span>
            </el-form-item>
            <el-form-item label="专科学号:">
              <span>{{ props.row.juniorNumber }}</span>
            </el-form-item>
            <el-form-item label="专科班级:">
              <span>{{ props.row.juniorClass }}</span>
            </el-form-item>
            <el-form-item label="专科年级:">
              <span>{{ props.row.juniorGrade }}</span>
            </el-form-item>
            <el-form-item label="主考院校:">
              <span>{{ props.row.mainAcademy }}</span>
            </el-form-item>
            <el-form-item label="专业代码:">
              <span>{{ props.row.majorNumber }}</span>
            </el-form-item>
            <el-form-item label="专业名称:">
              <span>{{ props.row.majorName }}</span>
            </el-form-item>
            <el-form-item label="准考证号:">
              <span>{{ props.row.examRegistrationNumber }}</span>
            </el-form-item>
            <el-form-item label="所在系部:">
              <span>{{ props.row.department }}</span>
            </el-form-item>
            <el-form-item label="班级代码:">
              <span>{{ props.row.gradeNumber }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
    </el-table>
    <!--页脚分页,这个一般直接交给框架去做自动分页-->
    <div class="footer">
      <el-pagination background
                     @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page="currentPage"
                     :page-sizes="counts"
                     :page-size="count"
                     layout="total, sizes, prev, pager, next, jumper"
                     :total="totalCount">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        //表格数据
        tableData: [],
        //当前页
        currentPage: 1,
        //总页数
        totalPage: 1,
        //每页显示的条数
        counts: [100, 200, 300, 400, 500],
        //传递给后台
        count: 100,
        //总条数
        totalCount: 1,
        //搜索下拉框选项
        itemSelect: 'name',
        searchKeys: '',
        department: [
          {
            value: '计算机与通信工程学院',
            label: '计算机与通信工程学院'
          }, {
            value: '商学院',
            label: '商学院'
          }, {
            value: '电子工程学院',
            label: '电子工程学院'
          }, {
            value: '自动化学院',
            label: '自动化学院'
          }],
        searchCondition: [
          {value: 'name', label: '学生姓名'},
          {value: 'number', label: '专科学号'},
          {value: 'department', label: '所在系部'},
          {value: 'mainAcademy', label: '主考院校'},
          {value: 'major', label: '报考专业'},
        ],
        loading: true
      }
    },
    methods: {
      //输入框中内容被改变或者每页显示条数改变
      handleSizeChange (val) {
        switch (val) {
          case 100:
          case 200:
          case 300:
          case 400:
          case 500:
            this.count = val
            break
        }
        this.handleCurrentChange(val)
      },
      //当前页数被改变
      handleCurrentChange (val) {
        // 将改变后的页数赋值给当前页
        this.currentPage = val
        this.searchInfo()
      },
      //查询学生信息
      searchInfo () {
        let item = this.itemSelect
        let key = this.searchKeys.trim()
        let condition = {
          currentPage: this.currentPage,
          count: this.count
        }
        if (item === 'name') {
          condition.name = key
        } else if (item === 'number') {
          condition.juniorNumber = key
        } else if (item === 'department') {
          condition.department = key
        } else if (item === 'mainAcademy') {
          condition.mainAcademy = key
        } else {
          condition.majorName = key
        }
        //执行搜索操作
        let url = this.url_request.ip_port_dev + this.url_request.root_request + '/student_check'
        this.axios.get(url, {
          params: condition
        }).then(response => {
          //分页信息对象
          let pageInfo = response.data.pageInfo
          this.currentPage = pageInfo.currentPage
          this.totalPage = pageInfo.totalPage
          this.count = pageInfo.count
          this.totalCount = pageInfo.totalCount
          //数据信息
          this.tableData = response.data.data
          this.loading = false
        }).catch(error => {
          this.loading = false
        }).finally(() => {
          this.loading = false
        })
      },
      //重置查询条件
      reset () {
        this.itemSelect = 'name'
        this.searchKeys = ''
        this.loading = true
        let url = this.url_request.ip_port_dev + this.url_request.root_request + '/student_check'
        this.axios.get(url, {
          params: {
            currentPage: 1,
            count: this.count
          }
        }).then(response => {
          //分页信息对象
          let pageInfo = response.data.pageInfo
          this.currentPage = pageInfo.currentPage
          this.totalPage = pageInfo.totalPage
          this.count = pageInfo.count
          this.totalCount = pageInfo.totalCount
          //数据信息
          this.tableData = response.data.data
          this.loading = false
        }).catch(error => {
          this.loading = false
        })
        this.loading = false
      }
    },
    //请求数据
    mounted () {
      let url = this.url_request.ip_port_dev + this.url_request.root_request + '/student_check'
      this.axios.get(url, {
        params: {
          currentPage: 1,
          count: this.count
        }
      }).then(response => {
        //分页信息对象
        let pageInfo = response.data.pageInfo
        this.currentPage = pageInfo.currentPage
        this.totalPage = pageInfo.totalPage
        this.count = pageInfo.count
        this.totalCount = pageInfo.totalCount
        //数据信息
        this.tableData = response.data.data
        this.loading = false
      }).catch(error => {
        this.loading = false
      }).finally(() => {
        console.log('finally')
        this.loading = false
      })
    }
  }
</script>

<style scoped>
  .footer {
    text-align: center;
  }

  .demo-table-expand {
    font-size: 0;
  }

  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }

  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>



