<template>
  <div>
    <el-form :model="form" :rules="rules" ref="login_form" label-width="100px"
             class="login-form">
      <p>用户登录</p>
      <el-form-item label="用户账号:" prop="username">
        <el-input v-model="form.username" clearable
                  prefix-icon="my-icon-user" placeholder="请输入用户账号"/>
      </el-form-item>
      <el-form-item label="用户密码:" prop="password">
        <el-input v-model="form.password" show-password
                  prefix-icon="my-icon-password"
                  @keyup.enter.native="login" placeholder="请输入用户密码"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="login">立即登录</el-button>
        <el-button @click="reset">重置表单</el-button>
        <el-popover placement="top" width="160" v-model="visible">
          <p>平台提供验证信息自助找回以及人工协助找回密码</p>
          <div style="text-align: right; margin: 0">
            <el-button size="mini" type="text" @click="ownRetrieve">自助找回</el-button>
            <el-button type="primary" size="mini" @click="personRetrieve">人工找回</el-button>
          </div>
          <el-button slot="reference">忘记密码</el-button>
        </el-popover>
      </el-form-item>
      <el-form-item>
        <router-link class="register" to="/register">新用户注册</router-link>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
  export default {
    name: 'Login',
    data () {
      return {
        form: {
          username: '',
          password: ''
        },
        visible: false,
        rules: {
          username: [
            {required: true, message: '请输入账号', trigger: 'blur'},
            {min: 6, max: 20, message: '用户账号应在6-20个字符之间', trigger: 'blur'}
          ],
          password: [
            {required: true, message: '请输入密码', trigger: 'blur'},
            {min: 6, max: 20, message: '密码应在6-20个字符之间', trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      login () {
        const vm = this
        const username = this.form.username
        const password = this.form.password
        this.$refs.login_form.validate((valid) => {
          if (valid) {
            //发送后台进行认证
            this.axios(vm.url_request.ip_port_dev + '/login', {
              method: 'post',
              data: {
                username: username,
                password: password
              }
            }).then(response => {
              let result = response.data.message
              let role = response.data.role
              let name = response.data.name
              if (result === vm.login_show.success) {
                vm.$router.replace('/main')
                //存储数据,窗口关闭会自动销毁,临时存储下
                sessionStorage.setItem('username', username)
                sessionStorage.setItem('name', name)
                sessionStorage.setItem('role', role)
              } else {
                this.$message({
                  title: '提示',
                  message: result,
                  type: 'error',
                  center: true
                })
              }
            }).catch(error => {
              this.$message({
                title: '提示',
                message: '系统出错,请稍后再试!',
                type: 'error',
                center: true
              })
            })
          } else {
            return false
          }
        })
      },
      reset () {
        this.$refs.login_form.resetFields()
      },
      //自助找回密码
      ownRetrieve () {
        this.$router.replace('/forgetPassByOwn')
      },
      personRetrieve () {
        this.$router.replace('/forgetPassByPerson')
      }
    }
  }
</script>

<style scoped>
  .login-form {
    width: 50%;
    text-align: center;
    margin: 100px auto;
  }

  .el-input {
    width: 80%;
  }

  .register {
    text-decoration: none;
    font-size: large;
    color: coral;
  }
</style>




