<template>
  <div>
    <el-button type="success" @click="showDialog(0)">添加学生</el-button>
    <el-select v-model="itemSelect" style="width: 120px">
      <el-option v-for="item in searchCondition" :key="item.value" :label="item.label"
                 :value="item.value">
      </el-option>
    </el-select>
    <el-input placeholder="输入关键字进行自动筛选" style="width: 200px"
              v-model="searchKeys" clearable/>
    <el-button icon="el-icon-search" plain @click="searchInfo">查询</el-button>
    <el-button icon="el-icon-refresh" plain @click="reset">重置</el-button>
    <!--表格主体内容部分 设置max-height需要设置height 否则不起作用-->
    <el-table :data="tableData" stripe border max-height="500" height="450"
              v-loading="loading" element-loading-text="拼命加载中"
              element-loading-spinner="el-icon-loading"
              element-loading-background="rgba(0, 0, 0, 0.8">
      <el-table-column label="序号" type="index" width="50"/>
      <el-table-column prop="name" label="姓名" width="80"/>
      <el-table-column prop="juniorNumber" label="专科学号"/>
      <el-table-column prop="juniorGrade" label="专科年级" width="80"/>
      <el-table-column prop="mainAcademy" label="主考院校"/>
      <el-table-column prop="sex" label="性别" width="50"/>
      <el-table-column prop="majorName" label="报考专业"/>
      <el-table-column prop="department" label="所属学院" width="200"/>
      <el-table-column label="操作" align="center">
        <template slot-scope="scope">
          <el-button size="mini" @click="showDialog(1,scope.$index)">编辑
          </el-button>
          <el-button size="mini" type="danger" @click="column_delete(scope.$index)">删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--页脚分页,这个一般直接交给框架去做自动分页-->
    <div class="footer">
      <el-pagination background
                     @size-change="handleSizeChange" @current-change="handleCurrentChange"
                     :current-page="currentPage" :page-sizes="counts"
                     :page-size="count"
                     layout="total, sizes, prev, pager, next, jumper" :total="totalCount">
      </el-pagination>
    </div>
    <!--弹出框嵌套一个表单-->
    <el-dialog :title="title" :visible.sync="dialogFormVisible"
               :center="true" :close-on-click-modal="false" @open="open">
      <el-form :model="form" :rules="rules" :inline="true" ref="dialog_form">
        <el-form-item label="学生姓名:" prop="name">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="身份证号:" prop="idNumber">
          <el-input v-model="form.idNumber"></el-input>
        </el-form-item>
        <el-form-item label="出生日期:" prop="birthDate">
          <el-date-picker v-model="form.birthDate" style="width: 200px;"
                          type="date" placeholder="请选择日期"/>
        </el-form-item>
        <el-form-item label="专科学号:" prop="juniorNumber">
          <el-input v-model="form.juniorNumber"></el-input>
        </el-form-item>
        <el-form-item label="专科班级:" prop="juniorClass">
          <el-input v-model.number="form.juniorClass"></el-input>
        </el-form-item>
        <el-form-item label="专科年级:" prop="juniorGrade">
          <el-input v-model.number="form.juniorGrade"></el-input>
        </el-form-item>
        <el-form-item label="主考院校:" prop="mainAcademy">
          <el-input v-model="form.mainAcademy"></el-input>
        </el-form-item>
        <el-form-item label="专业代码:" prop="majorNumber">
          <el-input v-model="form.majorNumber"></el-input>
        </el-form-item>
        <el-form-item label="专业名称:" prop="majorName">
          <el-input v-model="form.majorName"></el-input>
        </el-form-item>
        <el-form-item label="准考证号:" prop="examRegistrationNumber">
          <el-input v-model="form.examRegistrationNumber"></el-input>
        </el-form-item>
        <el-form-item label="所属学院:" prop="department">
          <el-select v-model="form.department" style="width: 200px;" placeholder="请选择学生系别">
            <el-option
              v-for="item in department"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="班级代码:" prop="gradeNumber">
          <el-input v-model="form.gradeNumber"></el-input>
        </el-form-item>
        <el-form-item label="学生性别:" prop="sex">
          <el-radio v-model="form.sex" label="男">男</el-radio>
          <el-radio v-model="form.sex" label="女">女</el-radio>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="ensure">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    name: 'StudentInfoMaintain',
    data () {
      return {
        //表格数据
        tableData: [],
        //当前页
        currentPage: 1,
        //总页数
        totalPage: 1,
        counts: [100, 200, 300, 400, 500],
        //每页显示的条数
        count: 100,
        //总条数
        totalCount: 1,
        //搜索下拉框选项
        itemSelect: 'name',
        searchKeys: '',
        title: '新增学生信息',
        dialogFormVisible: false,
        department: [
          {
            value: '计算机与通信工程学院',
            label: '计算机与通信工程学院'
          }, {
            value: '商学院',
            label: '商学院'
          }, {
            value: '电子工程学院',
            label: '电子工程学院'
          }, {
            value: '自动化学院',
            label: '自动化学院'
          }],
        searchCondition: [
          {value: 'name', label: '学生姓名'},
          {value: 'number', label: '专科学号'},
          {value: 'department', label: '所在系部'},
          {value: 'mainAcademy', label: '主考院校'},
          {value: 'major', label: '报考专业'},
        ],
        form: {
          id: -1,
          name: '',
          idNumber: '',
          birthDate: '',
          juniorNumber: '',
          juniorClass: '',
          juniorGrade: '',
          mainAcademy: '',
          majorNumber: '',
          majorName: '',
          examRegistrationNumber: '',
          department: '计算机与通信工程学院',
          sex: '男',
          gradeNumber: ''
        },
        loading: true,
        //验证规则
        rules: {
          name: [
            {required: true, message: '请输入学生性名'},
            {min: 2, max: 6, message: '学生姓名应在2-6个字符之间'}
          ],
          idNumber: [
            {required: true, message: '身份证号为必输项'},
            {min: 10, max: 20, message: '身份证号应在10-20个字符之间'}
          ],
          birthDate: [{required: true, message: '出生日期为必选项'}],
          juniorNumber: [
            {required: true, message: '专科学号为必输项'},
            {min: 8, max: 20, message: '专科学号应该8-20个字符之间'}
          ],
          juniorClass: [
            {required: true, message: '专科班级为必输项'},
            {
              type: 'number', message: '专科班级需要是数字',
              min: 1, max: 999999999, message: '专科班级应该在1-999999999之间'
            }
          ],
          juniorGrade: [
            {required: true, message: '专科年级为必输项'},
            {
              type: 'number', message: '专科年级需要是数字',
              min: 1, max: 999999999, message: '专科年级应该在1-99999999之间'
            }
          ],
          mainAcademy: [
            {required: true, message: '主考院校为必输项'},
            {min: 6, max: 20, message: '主考院校应在6-20个字符之间'}
          ],
          majorNumber: [
            {required: true, message: '专业代码为必输项'},
            {min: 6, max: 20, message: '专业代码应在6-20个字符之间'}
          ],
          majorName: [
            {required: true, message: '专业名称为必输项'},
            {min: 4, max: 20, message: '专业名称应在4-20个字符之间'}
          ],
          examRegistrationNumber: [
            {required: true, message: '准考证号为必输项'},
            {min: 6, max: 20, message: '准考证号应在6-20个字符之间'}
          ],
          sex: [
            {required: true}
          ],
          department: [
            {required: true, message: '请选择学生系别'},
            {min: 6, max: 20, message: '所属学院应在6-20个字符之间'}
          ],
          gradeNumber: [
            {required: true, message: '请输入学生班级代码'},
            {min: 6, max: 20, message: '学生班级代码应在6-20个字符之间'}
          ]
        }
      }
    },
    methods: {
      /*
      *弹出框弹出的回调方法
      * @param type 打开方式
      * @param data 数据
      * */
      open (type, data) {
        //必须在该方法中才可以实现表单的重置
        this.$nextTick(() => {
          this.$refs.dialog_form.resetFields()
          switch (type) {//1编辑2添加
            case 0:
              this.title = '新增学生信息'
              break
            case 1:
              this.title = '编辑学生信息'
              this.form = data
              break
            default:
              break
          }
        })
      },
      //列的删除方法
      column_delete (index, row) {
        this.$confirm('您确定要删除该条学生信息吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消'
        }).then(() => {
          //执行删除操作
          let id = this.tableData[index].id
          let url = this.url_request.ip_port_dev +
            this.url_request.root_request + '/student_manage/delete'
          this.addOrUpdateOrDelete(url, {id: id})
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
      },
      /**
       * 显示对话框
       * @param what 0表示新增1表示编辑
       * @param index 当前数据的下标,只有编辑时候才有
       */
      showDialog (what, index) {
        this.dialogFormVisible = true
        if (what === 0) {
          this.open(0)
        } else {
          this.title = '编辑学生信息'
          //拷贝一份，否则影响源数据
          let object = Object.assign({}, this.tableData[index])
          this.open(1, object)
        }
      },
      //对话框确定事件
      ensure () {
        //表单校验
        this.$refs.dialog_form.validate((validate) => {
          if (validate) {
            //向后台传递数据
            let id = this.form.id
            let name = this.form.name
            let idNumber = this.form.idNumber
            let birthDate = this.form.birthDate
            let juniorNumber = this.form.juniorNumber
            let juniorClass = this.form.juniorClass
            let juniorGrade = this.form.juniorGrade
            let mainAcademy = this.form.mainAcademy
            let majorNumber = this.form.majorNumber
            let majorName = this.form.majorName
            let examRegistrationNumber = this.form.examRegistrationNumber
            let department = this.form.department
            let sex = this.form.sex
            let gradeNumber = this.form.gradeNumber
            let studentInfo = {
              id, name, idNumber, birthDate, juniorNumber,
              juniorClass, juniorGrade, mainAcademy,
              majorNumber, majorName, examRegistrationNumber, department,
              sex, gradeNumber
            }
            let url = this.url_request.ip_port_dev + this.url_request.root_request
            //判断是新增还是编辑
            if (id < 0) {
              //新增
              url += '/student_manage/add'
              this.addOrUpdateOrDelete(url, studentInfo)
            } else {
              //编辑
              url += '/student_manage/update'
              this.addOrUpdateOrDelete(url, studentInfo)
            }
            this.dialogFormVisible = false
          } else {
            return false
          }
        })
      },
      //查询学生信息
      searchInfo () {
        let item = this.itemSelect
        let key = this.searchKeys.trim()
        let condition = {
          currentPage: this.currentPage,
          count: this.count
        }
        if (item === 'name') {
          condition.name = key
        } else if (item === 'number') {
          condition.juniorNumber = key
        } else if (item === 'department') {
          condition.department = key
        } else if (item === 'mainAcademy') {
          condition.mainAcademy = key
        } else {
          condition.majorName = key
        }
        //执行搜索操作
        let url = this.url_request.ip_port_dev + this.url_request.root_request + '/student_check'
        this.axios.get(url, {
          params: condition
        }).then(response => {
          //分页信息对象
          let pageInfo = response.data.pageInfo
          this.currentPage = pageInfo.currentPage
          this.totalPage = pageInfo.totalPage
          this.count = pageInfo.count
          this.totalCount = pageInfo.totalCount
          //数据信息
          this.tableData = response.data.data
          this.loading = false
        }).catch(error => {
          this.loading = false
        })
      },
      //重置查询条件
      reset () {
        this.itemSelect = 'name'
        this.searchKeys = ''
        this.loading = true
        let url = this.url_request.ip_port_dev + this.url_request.root_request + '/student_check'
        this.axios.get(url, {
          params: {
            currentPage: 1,
            count: this.count
          }
        }).then(response => {
          //分页信息对象
          let pageInfo = response.data.pageInfo
          this.currentPage = pageInfo.currentPage
          this.totalPage = pageInfo.totalPage
          this.count = pageInfo.count
          this.totalCount = pageInfo.totalCount
          //数据信息
          this.tableData = response.data.data
          this.loading = false
        }).catch(error => {
          this.loading = false
        })
      },
      /**
       * 添加或者更新或者删除学生信息
       * @param url 请求的地址
       * @param student 学生信息对象实体
       */
      addOrUpdateOrDelete (url, student) {
        //执行新增或者更新操作
        this.axios(url, {
          method: 'post',
          data: student
        }).then(response => {
          let result = response.data
          this.reset()
          this.$message({
            title: '提示',
            message: result,
            type: 'success',
            center: true
          })
        }).catch(error => {
          this.$message({
            title: '提示',
            message: '系统出错,请稍后再试!',
            type: 'error',
            center: true
          })
        })
      },
      //输入框中内容被改变
      handleSizeChange (val) {
        switch (val) {
          case 100:
          case 200:
          case 300:
          case 400:
          case 500:
            this.count = val
            break
        }
        this.handleCurrentChange(val)
      },
      //当前页数被改变
      handleCurrentChange (val) {
        //将改变后的页数赋值给当前页
        this.currentPage = val
        this.searchInfo()
      }
    }
    ,
    mounted () {
      let url = this.url_request.ip_port_dev + this.url_request.root_request + '/student_check'
      this.axios.get(url, {
        params: {
          currentPage: 1,
          count: this.count
        }
      }).then(response => {
        //分页信息对象
        let pageInfo = response.data.pageInfo
        this.currentPage = pageInfo.currentPage
        this.totalPage = pageInfo.totalPage
        this.count = pageInfo.count
        this.totalCount = pageInfo.totalCount
        //数据信息
        this.tableData = response.data.data
        this.loading = false
      }).catch(error => {
        this.loading = false
      })
    }
  }
</script>

<style scoped>
  .el-input {
    width: 200px;
  }

  .el-table {
    width: 100%;
    margin-top: 10px
  }

  .footer {
    text-align: center;
  }
</style>



